import React, { useState, useEffect } from 'react'
import './TopSection.scss'
import img1 from '../../../../assets/images/top-section/1.webp'
import img2 from '../../../../assets/images/top-section/2.webp'
import img3 from '../../../../assets/images/top-section/3.webp'

import logo from '../../../../assets/images/logos/long-logo.png'
import tiktokImg from '../../../../assets/images/socials/tiktok.svg'
import instagramImg from '../../../../assets/images/socials/instagram.svg'
import twitterImg from '../../../../assets/images/socials/twitter.svg'

import MintSection from '../../../../components/mint-section/MintSection'
import Timer from '../../../../misc/timer'

function TopSection({ setWalletOpen }) {
    const [width, setWidth] = useState()
    const openLink = (link) => {
        window.open(link)
    }
    useEffect(() => {
        window.addEventListener('resize', () => {
            setWidth(window.innerWidth)
        })
    })

    useEffect(() => {
        let img1 = document.getElementById('top-section-img-1')
        let img2 = document.getElementById('top-section-img-2')
        let img3 = document.getElementById('top-section-img-3')

        let timer = new Timer(() => {
            img1.style.height = '40%'
            img1.style.left = '54.2%'
            img1.style.bottom = '13.5%'
            img1.style.zIndex = '1'

            img2.style.height = '43%'
            img2.style.left = '0'
            img2.style.bottom = '2%'
            img2.style.zIndex = '1'

            img3.style.height = '62%'
            img3.style.left = '50px'
            img3.style.bottom = '37.3%'
            img3.style.zIndex = '0'

            let oldImg1 = img1;

            img1 = img3
            img3 = img2
            img2 = oldImg1
        }, [2500])
        timer.stop()
        if (window.innerWidth > 525) {
            timer.start()
        }
        window.addEventListener('resize', () => {
            if (window.innerWidth < 525) {
                timer.stop()
            }
            else {
                timer.stop()
                timer.start()
            }
        })
    })

    return (
        <div className='top-section'>
            {/* <span style={{color:'white',position:'fixed'}}>{width}</span>  */}
            <div className='top-section-inner'>
                <img className='top-section-logo' src={logo} />
                <div className='top-section-bottom'>
                    <div className='top-section-img-wrapper'>
                        <img id='top-section-img-1' src={img1} />
                        <img id='top-section-img-2' src={img2} />
                        <img id='top-section-img-3' src={img3} />
                    </div>
                    <div className='top-section-info-wrapper'>
                        <p className='top-section-title ffam-edo tc-blue fs-xl'>
                            Welcome to
                            <br />
                            Party Apes Club
                        </p>
                        <p className='top-section-info  ffam-lato tc-white fw-light fs-xs'>
                            Party Apes Club (PAC) is an inclusive community with one common purpose; to MOVE, GROW, and THRIVE.  PAC offers exclusive members-only benefits that includes access to Special Events, LIVE Music, Wellness Products and other exciting perks and utilities.  Your PAC NFT also doubles as your membership to all future PAC and BBW events.
                        </p>
                        <p className='top-section-encourage  ffam-edo tc-white fs-l'>
                            Together we MOVE, GROW, and THRIVE.
                        </p>
                        {/* <div className='top-section-btn-wrapper'>
                            <button id='top-section-connect-btn-1' className='top-section-connect-btn'>
                                Join Now Via Wallet
                            </button>
                            <button className='top-section-connect-btn'>
                                Join Now Via Credit Card
                            </button>
                        </div> */}
                        <MintSection setWalletOpen={setWalletOpen} />

                        {/* <div className='top-sectin-social-wrapper'>
                            <button onClick={()=>{openLink('https://twitter.com/PartyApesClub')}} className='social-btn' >
                                <img src={twitterImg}/>
                            </button>
                            <button onClick={()=>{openLink('https://www.instagram.com/partyapesclub/')}} className='social-btn'>
                                <img src={instagramImg}/>
                            </button>
                            <button onClick={()=>{openLink('https://www.tiktok.com/@partyapesclub')}} className='social-btn'>
                                <img src={tiktokImg}/>
                            </button>

                        </div> */}
                    </div>
                </div>

            </div>
        </div>
    )
}

export default TopSection