import React, { useState } from 'react';
import './FaqSection.scss'
import Faq from './Faq'

import dropDownImg from '../../../../assets/images/drop-down.png'

function FaqSection() {
    const [faqOpen, setFaqOpen] = useState(false)
    const expandFaq = () => {
        let faq = document.getElementsByClassName('faq-section-inner-main')[0]
        let faqHeight = window.getComputedStyle(document.getElementsByClassName('faq-section-inner')[0]).height

        // let addHeight=window.innerWidth<500?200:30
        faqHeight = `${Number(faqHeight.substring(0, faqHeight.length - 2)) + 30}px`
        console.log(faqHeight)
        if (faqOpen === false) {
            faq.style.height = faqHeight;
            setTimeout(() => {
                faq.style.overflow = 'visible'
                faq.style.height = 'fit-content';
            }, [300])
        }
        else {
            faq.style.height = faqHeight;
            setTimeout(() => {
                faq.style.height = '0px';
            }, [20])
            // setTimeout(()=>{
            faq.style.overflow = 'hidden'
            // },[300])
        }
        setFaqOpen(!faqOpen)
    }

    return (
        <div className='faq-section'>
            <div onClick={() => { expandFaq() }} className='faq-section-title ffam-edo tc-blue fs-xls'>
                FAQ's
                <img src={dropDownImg}
                    style={{ transform: faqOpen === true ? 'rotateZ(-180deg)' : 'rotateZ(0deg)' }}
                />
            </div>
            <div className='faq-section-inner-main'>
                <div className='faq-section-inner'>
                    <Faq
                        question='When is the launch?'
                        answer="
                        The GENESIS COLLECTION will be released to the public on 11/11 at 4pm UTC.  Presale/ Allow List will commence 24 hours before on 11/10. 
                        <splithere>
                        The remaining apes will then be released for purchase during Phase 2.  Please make sure to stay up to date for announcements via our Discord and Twitter account @partyapesclub.
                    "
                    />
                    <Faq
                        question='What platform is PAC on and how can I purchase an NFT?'
                        answer="
                        We will be on the Ethereum platform, and you will be able to mint via digital wallets such as Metamask, Coinbase, Wallet Connect and more.  We highly suggest using a burner wallet when minting.
                        <splithere>

                        You can also mint via credit card through Crossmint (not available during presale).  Simply click on JOIN via CREDIT CARD.  Only an email address and credit card is needed to purchase and have a digital wallet automatically created for you.  We highly recommend this option if this is the first time purchasing an NFT.  You can learn more 
                        <linkhere>here<<<linkhere>.

                        <splithere>
                        The collection will then be available for purchase in the secondary market via Opensea and Rarible after the collection has launched and sold out.
                    "
                        link="https://www.youtube.com/watch?v=dAWs6lpTzg4"
                    />
                    {/* <Faq
                        question='What Wellness Benefits are included for PAC members?'
                        answer="The PAC is a community focused on celebrating life, supporting one another, and helping each other improve both physically and mentally. PAC members will enjoy wellness tools that give you access to therapists, wellness coaches, meditations, community support and much more. Members can view and claim their benefits via the PAC PERK SHOP where you can get access to wellness benefits and products, giveaways, merch, NFTs, event tickets and more.
                        <splithere>
                        All benefits are FREE without any copays and insurance is NOT needed to access benefits.
                        <splithere>
                        You can access all our benefits by becoming a PAC member for .28 ETH during Public Sale.
                        <splithere>
                        Click <linkhere>here<<<linkhere> for a sneak peek of our upcoming perks for PAC holders.
                        "
                        link="https://perk.shop/pac"
                    />
                    <Faq
                        question='What happens once the wellness benefits have been claimed?'
                        answer="Our founders believe that everyone should have access to healthcare, therefore, they have made long-term strategic partnerships to be able to extend healthcare to PAC members and their immediate family at a discounted price. Why their family? Because we believe that no one can truly THRIVE alone. We THRIVE as a PACk!"
                    
                    /> */}
                    <Faq
                        question='Where can I view and claim giveaways?'
                        answer="You can use the Token Checker located in the PAC PERK SHOP <linkhere>here<<<linkhere>: Party Apes Club - Perk Shop"
                        link="https://perk.shop/pac"

                    />
                    <Faq
                        question='How many apes are in the PAC?'
                        answer="
                        There is a total of 11k PAC apes from the Meeyami Jungle.  The first phase for adoption will consist of 1,111 apes which will be a part of our elite GENESIS COLLECTION.  These apes will have a limited number of special traits and benefits that will be exclusive to this collection and not seen again in Phase 2. 
                        <splithere>
                        For fair distribution, the PAC team will only be allowed to randomly mint a total of 22 apes during presale which will be used for partnerships, charities, giveaways, and member access to the club. 
                    "
                    />
                    {/* <Faq
                        question='What is the story behind the PAC?'
                        answer="(video to link here)"
                    /> */}
                    {/* <Faq
                        question='Do you have an Allow List?  If so, how can I be a part of it?'
                        answer="
                        Yes. You can win a chance of being a part of our allow list by entering our giveaways and joining our twitter spaces twice a week.  You can also download the PACAPE app and tag @partyapesclub.  We will be looking for PAC friends to invite to our Allow List minting on 11/10.
                     
                    "
                    /> */}
                    <Faq
                        question='Will PAC be revealed right after mint or is there a waiting period?'
                        answer="
                        Nope!  The PAC has been patiently waiting to be rescued, therefore, you will immediately be able to see the apes that you have rescued. 
                    "
                    />
                    {/* <Faq
                        question='What is The Meeyami Jungle and when will it open for members?'
                        answer="
                        The Meeyami Jungle is the only haven and home of the PAC. Here, only members of the PAC will be allowed to enter and unite with the rest of the PAC.  Once united, members will be able to chat, party, collaborate, and support one other as an exclusive and conscious community.  Our manifesto binds us as a group who congregates to move, grow and thrive together so that we can elevate and celebrate life as a PACk. 
                        <splithere>
                        The Meeyami Jungle will open for MEMBERS ONLY during mint on 11/11. 
                    "
                    /> */}
                </div>
            </div>

           
        </div>
    );
}

export default FaqSection;
